<template lang="pug">.mycalendar
    //- div currentMonth {{currentMonth}}
    //- div calendarArr {{calendarArr.length}}   
    div monthCheckPoints {{monthCheckPoints}}   
    //- div scrollDirection {{scrollDirection}}
    .mycalendar__header
      //- .mycalendar__handler.prev(@click="monthBack")
      span {{monthList[currentMonth]}}
      //- .mycalendar__handler.next(@click="monthForw")

    //- div {{currentMonth}}
    //- div {{getCurMonth}}
    .mycalendar__content
      .calendar__wrapper(
        @scroll="calendarWrapperScroll"
        ref="wrapper")
          .mycalendar__cell(v-for="(cell, ind) in calendarArr" :key="`cell${ind}`"
            :class="{'active': cell.isActiveDay}"
            :date-text="cell.textDate")
              .mycalendar__dayname {{cell.dayName}}
              .mycalendar__value {{cell.dayOfWeekInd}}
      //- .div(@click="currentMonth + 1") next
//- div {{getDaysInMonthCount}}
//- div {{currentMonth}}
//- div getPrevMonthDays {{getPrevMonthDays.length}}
//- .mycalendar__title {{getCurMonth}}
//- .mycalendar__content
//-  swiper(
//-    ref="mySwiper"
//-    :options="swiperOptions"
//-    :initialSlide="getPrevMonthDays.length + swiperOptions.slidesPerView"
//-    @slide-prev-transition-start="backward"
//-    @slide-next-transition-start="forward"
//-    @slide-change-transition-start="transitionStart")
//-      swiper-slide(v-for="(cell, ind) in calendarArr" :key="`cell${ind}`")
//-        .mycalendar__cell
//-           .mycalendar__dayname {{cell.dayName}}
//-           .mycalendar__value {{cell.dayOfWeekInd}}
</template>

<script>
	// import { VueAgile } from 'vue-agile'
	// import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper'
	// import createCalendar from '@/functions/createCalendar'
  import horizontalCalendar from '@/functions/createCalendarWithMonthsScroll'

export default {

  name: 'MyCalendar',
  data () {
    return {
    	currentDate: new Date(),
    	currentYear: new Date().getFullYear(),
    	currentMonth: new Date().getMonth(),

    	calendarArr: [],
      monthCheckPoints: [],
     
      initialMonth: new Date().getMonth(),
    	
      monthList: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'],
    	activeDates: [new Date(2021, 4, 4), new Date(2021, 4, 24),  new Date(2021, 4, 14)]
    }
  },
  computed: {

  },
  methods: {
  	monthForw(){
      console.log('this.monthForw');

  		this.currentMonth += 1;
  		
  		if(this.currentMonth > 11) {
  			this.currentMonth = 0;
  			this.currentYear += 1;
  		}

  	},
  	monthBack(){
  		this.currentMonth -= 1;
  		
  		if(this.currentMonth < 0) {
  			this.currentMonth = 11;
  			this.currentYear -= 1;
  		}


  	},

    widthWrapper(){
      return this.$refs.wrapper.offsetWidth;
    },
    dayWidth(){
      return this.widthWrapper() /  100 * 14.3;
    },
  	calendarWrapperScroll(evt){
      const scrollLeft =  evt.target.scrollLeft;

      this.monthCheckPoints.forEach((point, i, arr) => {
      // let val = prev + curr;
      // console.log('arr[i] ' + arr[i]);
      // console.log('arr[i+1] ' + arr[i+1]);

        if(scrollLeft >= arr[i] && scrollLeft <= arr[i + 1]){
          // console.log('prev ' + arr[i]);
          this.currentMonth = arr.indexOf(arr[i+1]);
        }
        if(scrollLeft < arr[0]){
          this.currentMonth = 0;
        }
        
       });
        
      if(scrollLeft > this.monthCheckPoints[this.monthCheckPoints.length - 1] - 50){
        this.currentMonth = 0;
      }

  		// console.log(this.widthWrapper());
    //   console.log(this.dayWidth());
      // console.log(evt.target);
  		// console.log(evt.target.offsetWidth + evt.target.scrollLeft);
  		// console.log(evt.target.scrollWidth);

    },
    setMonthCheckpoints(){
      console.log(this.dayWidth());
      this.monthCheckPoints = horizontalCalendar(this.dayWidth()).init().monthCheckPoints;
    }
  },
  created(){

  },
  mounted(){
    console.log(this.$refs.wrapper);
    this.calendarArr = horizontalCalendar().init().fullYearArr;
    this.monthCheckPoints = horizontalCalendar(this.dayWidth()).init().monthCheckPoints;
    window.addEventListener('resize', this.setMonthCheckpoints);

    let scroll; //величина скролла к текущему месяцу
    
    if(this.currentMonth > 0 && this.currentMonth < 12){
      scroll = this.monthCheckPoints[this.currentMonth - 1];
    }
    if(this.currentMonth == 0){
      scroll = 3;
    }
    console.log(scroll);
    setTimeout(() => {this.$refs.wrapper.scrollTo(scroll + 3, 0);}, 100)
    
    console.log(this.$refs.wrapper.scrollLeft);
  },
  beforeDestroy(){
    window.removeEventListener('resize', this.setMonthCheckpoints);
  }
}
</script>

<style lang="scss">
	@import '@/assets/scss/misc/_vars.scss';
	.mycalendar{
    padding: 0 24px;
    
    background-color: #fff;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
		
    &__content{
			display: flex;
			align-items: center;

		}
	}
	.calendar__wrapper{
    padding-bottom: 37px;
		display: flex;
		flex: 1 0 0;
		overflow-x: auto;
	}
	.mycalendar__cell{
		// width: 40px;
		width: 14.3%;
		flex-shrink: 0;
		text-align: center;
		user-select: none;

    &.active{
      .mycalendar__value{
        color: #fff;
        background-color:#0084F4;
      }
    }
	}
	.mycalendar__dayname{
  	margin-bottom: 12px;
		font-size: 11px;
		font-family: $sf-t-reg, sans-serif;
		color: rgb(160, 174, 192);
		text-transform: uppercase;
		font-weight: 400;
	}
	.mycalendar__value{
    width: 25px;
    height: 25px;
    margin: 0 auto;
    line-height: 25px;
    text-align: center;
    border-radius: 100%;
		font-size: 15px;
	}
  .mycalendar__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 26px;
    font-size: 34px;
    line-height: 41px;
    font-family: $sf-t-semi;
  }
  .mycalendar__handler{
    width: 30px;
    background-repeat: no-repeat;
    background-position: center;

    &.prev{
      background-image: url(~@/assets/img/icons/modal/calendar-prev.svg);
    }
    &.next{
      background-image: url(~@/assets/img/icons/modal/calendar-next.svg);
    }
  }

</style>